<script>
export default {
  name: 'ReviewComponent',
  data() {
    return {
      searchQuery: '',
    };
  },
  methods: {
    matches(name) {
      const q = (this.searchQuery || '').trim().toLowerCase();
      if (!q) return true;
      return name.toLowerCase().includes(q);
    },
  },
};
